export default {
    mounted() {
        const scene = this.$scrollmagic
        .scene({
            triggerElement: "#img-wrapper-trigger",
            triggerHook: 0.4,
            duration: 800,
        })
        .setTween('#img-wrapper-png', {
            css: {
                transform: 'translateY(70px)',
                transion: 'ease'
            },
        });
        this.$scrollmagic.addScene(scene);
    }
};
